@media only screen and (min-width:700px) {
    $border: 3px solid #000;
    //NAV

    nav{
        .c-nav-info-btn__product{
            display: flex;

            a{
                font-size: 12px;
                padding: 12px 26px;

                img{
                    width: 10px;
                }
            }

            & > a:last-child{
                img{
                    width: 30px;
                }
            }
        }

        & > .logo{ 
            display: block;
            img{
                width: 160px;
            }
        }

        .c-nav-info-btn__about{
            
            img{
                width: 30px;
            }

            .c-link-btn{
                border: $border;
                padding: 14px 20px;
                font-size: 16px;
                border-radius: 100px;
                margin-right: 25px;
            }
        }

        .c-nav-info-btn__search{
            button{
                img{
                    width: 24px;
                }
            }
    
            input{
                padding: 2px 0 2px 20px;
                margin-left: 20px;
                font-size: 24px;
            }
        }

        .c-nav-cart, .c-nav-info-btn__product{
            img{
                width: 30px;
            }
        }
    }

    .o-mob-logo{
        display: none;
    }


    //LOADER

    .c-loader{
        & > img{
            top: 22%;
        }
    
        & > div:last-child{
            bottom: 2%;
            width: 21%;
        }
    }


    //MAIN

    main{
        padding: 8% 5% 0 5%;
    }



    //HOME

    .c-home{
        padding: 0 20%;
    
        & > div{
            width: 35%;
        }
    }

    .c-home-logo{
        display: none;
    }


    //PRODUCT

    .o-product-flex{
        display: flex;

        .c-product-img{
            width: 48%;
        }

        .c-product-info{
            padding: 0 0 0 5vw;
            width: 52%;
        }
    }

    .c-product-title{
        padding-bottom: 10px;
        h2{
            font-size: 28px;
        }
    }

    .c-product-cta{
        position: relative;
        width: 100%;
        padding: 0 0;

        .c-size-selector{
            button{
                font-size: 20px;
                width: 75px;
                height: 75px;
            }
        }

        & > button{
            padding: 40px 0;
            font-size: 22px;
        }
    }

    .c-product-para{
        margin-bottom: 80px;

        p{
            font-size: 12px;
        }
    }


    .c-product-size{
        left: 18%;
        top: 10%;
        min-height: 470px;
        height: max-content;
        padding: 0 2%;
        width: 64%;

        & > div{
            display: flex;
            flex-shrink: 1;
        }

        & > div > div:first-child{
            width: 60%;
            padding: 80px 40px;
            position: relative;
    
            .o-close{
                top: 24px;
                left: 6px;
    
                img{
                    width: 18px;
                }
            }
        }

        #js-shoulder{
            top: 17%;
            left: 44%;
        }
    
        #js-chest{
            top: 60%;
            left: 44%;
        }
    
        #js-arm{
            top: 41%;
            left: 17%;
        }
    
        #js-torso{
            right: 4%;
            bottom: 35%;
        }

        p, li{
            font-size: 12px;
            padding-bottom: .6rem;
        }

        & > div > div:last-child{
            margin-left: 20px;
            padding: 35px;
            height: max-content;

            hr{
                margin-bottom: 5px;
            }

            div{
                margin-bottom: 15px; 
            }  
        }
    }


    .c-product-nav-btns{
        left: 5%;
        bottom: 5%;

        a{
            border: $border;
            padding: 12px 26px;
            display: flex;
            font-size: 14px;
            align-items: center;
        }
    
        a:first-child{
            margin-right: 20px;
            img{
                margin: 0 17px 0 0;
            }
        }
    
        a:last-child{
            margin-right: 5px;
            img{
                transform: rotate(-180deg);
                margin: 0 0 0 17px;
            }
        }
    }





    //ACCOUNT


    .c-account{

        .o-close{
            width: 100%;
            height: 100%;
            background-color: transparent;
            border-radius: 0;
            top: 0;
            left: 0;
        }

        form{
            & > button{
                padding: 35px 0;
                font-size: 24px;
                border: none;
                background-color: #000;
                color: #fff;
            }

            label{
                font-size: 20px;
            }

            input{
                font-size: 28px;
            }
        }

        & > div{
            width: 47%;
            z-index: 6;
            position: absolute;
            right: 0;
            border: $border;

            img{
                width: 170px;
                margin-bottom: 40px;
            }
            
            & > div{
                padding: 0 16%;
            }
        }
    }



    //CART/BAG

    .c-bag{
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    
        .o-close{
            width: 100%;
            height: 100%;
            border-radius: 0;
            background-color: transparent;
            position: absolute;
            left: 0;
            top: 0;
            z-index: 1;

            p{
                display: none;
            }
        }
    
        & > div{
            width: 45%;
            border: $border;
            position: absolute;
            right: 0;
            z-index: 4;
            padding: 3.2% 2%;
    
            & > img{
                margin: 0 auto 30px auto;
                width: 150px;
                position: relative;
                top: 0;
                left: 0;
            }
        }

        .c-bag-item-wrap{
            margin-top: 2%;
        }
    }

    .c-bag-item{
        h3, select, p{
            font-size: 20px;
        }
    }


    //ABOUT

    .c-about{

        & > div:nth-child(2){
           top: 12%; 
        }
        

        .c-about-links{
            background-color: #F6F7EF;
            padding: 30px 30px;
            border: $border;
            width: 280px;
            a{
                display: block;
                margin: 8px 0;
                font-size: 20px;
            }
        }
    }


    //DETAILS

    .c-about-details{
        top: 14%;
        width: 40%;
        left: 30%;
    
        & > div{
            position: absolute;
            top: 0;
            left: 5%;
            width: 90%;
            padding: 0 5%;
            border: $border;
        }
    
        #js-info{
            background: url(/assets/info.png);
            background-size: 100%;
            background-position: center;
        }
    }



    //CATALOGUE

    .c-catalogue-section{
        margin: 2% 0 5%;
        h3{
            margin-bottom: 20px;
        }
    }
}